.wrapper {
    width: 100%;
    height: 100%;
    position: fixed;

    display: flex;
    justify-content: center;
    z-index: -1000;
}

.section {
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: center;
    column-gap: 60px;
}

.labelWrapper {
    width: 320px;
    height: 65px;
    padding: 22px 0;
    box-sizing: border-box;
}

.label {
    text-decoration: none;
    color: #8585ad;
}

.activeLabel {
    color: #f2f2f3;
    text-decoration: none;
}

.navHint {
    width: 320px;
    color: #8585ad;
    opacity: 0.4;
}

.content {
    width: 860px;
}
